@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Special+Elite&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Source+Code+Pro:wght@200&display=swap);
*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* Light palette */
  --primary-color: #f7f7e6;
  --secondary-color: rgb(252, 226, 177);
  --dark-shadow: rgba(0, 0, 0, 0.5);
  --lap-results: #f0c7b5af;
  --light-text: grey;
  --dark-text: black;
  --keys-shadow: rgba(0, 0, 0, 0.5);
  --border-color: #4e5255;
  --accent-text1: #b6590d;
  --accent-text2: #e06d10;
  --accent-text3: #f4a15d;
  --keys: white;
  --keys-text: black;
  --tracker: rgb(233, 255, 206);
  --border-75-transparent: #d6e9f5;
  background-color: var(--primary-color);
  height: 100vh;
}

.App-dark {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* Dark palette  */
  --primary-color: #242323;
  --secondary-color: rgb(61, 59, 56);
  --dark-shadow: black;
  --keys-shadow: black;
  --lap-results: #1a253e;
  --dark-text: #d6e9f5;
  --light-text: #7d8081;
  --keys: rgb(92, 86, 86);
  --tracker: rgb(26, 27, 24);
  --keys-text: #d6e9f5;
  background-color: var(--primary-color);
  height: 100vh;
}

body {
  font-family: "Roboto", sans-serif;
}

.light--toggler {
  color: white;
}

.dark--toggler,
.light--toggler {
  position: absolute;
  bottom: 8%;
  right: 11%;
  font-size: x-large;
  cursor: pointer;
}

.header--logo {
  position: absolute;
  left: 3%;
  top: 3%;
  height: 40px;
}

.start--typing--dark,
.start--typing {
  font-size: 1.7rem;
  font-weight: 500;
  position: absolute;
  top: 5%;
}

.start--typing--dark {
  color: white;
}

.start--typing {
  color: black;
}

#pointer--hand {
  position: absolute;
  width: 1.5rem;
  bottom: 0;
  left: 50%;
  transform: translate(-80%, 100%);
}

.point {
  animation: point 0.5s infinite ease-out alternate;
}

@keyframes point {
  from {
    transform: translate(-80%, 130%);
  }
  to {
    transform: translate(-80%, 100%);
  }
}

.text {
  display: flex;
  flex-wrap: wrap;
  padding: 1rem;
  width: 40rem;
}

.text > p {
  font-family: "Special Elite", cursive;
  font-size: 2rem;
  line-height: 1.5;
}

.remaining--letter {
  color: var(--light-text);
}

.previous--letter {
  color: var(--dark-text);
}

.keyboard--key,
.spacebar {
  display: inline-block;
  padding: 10px;
  margin: 2px;
  border-radius: 5px;
  min-width: 40.5px;
  text-align: center;
  font-weight: 500;
  background-color: var(--keys);
  box-shadow: 0px 2.98256px 7.4564px var(--keys-shadow);
  color: var(--keys-text);
}

.active-key__correct {
  transition: all ease;
  border-bottom: green;
  box-shadow: 0 0 1rem green;
  transform: scale(1.1);
}

.active-key__wrong {
  border-bottom: red;
  box-shadow: 0 0 1rem red;
  transform: scale(1.1);
}

.keyboard--top--row {
  position: absolute;
  top: 0;
}
.keyboard--mid--row {
  position: absolute;
  top: 25%;
}
.keyboard--bot--row {
  position: absolute;
  top: 50%;
}

.keyboard--container {
  align-items: center;
  display: flex;
  height: 200px;
  justify-content: center;
  min-width: 70%;
  position: relative;
}

.spacebar--row {
  position: absolute;
  top: 75%;
  width: 160px;
}

.spacebar {
  width: 100%;
}

.tracker--panel--container {
  position: absolute;
  top: 36%;
  transform: translateY(-36%);
  right: 8%;
  height: 32%;
  min-width: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.speed--panel,
.accuracy--panel {
  border-radius: 10px;
  height: 26%;
  padding: 6px;
  width: 100%;
  text-align: center;
  box-shadow: 0px 3px 7.5px var(--dark-shadow);
  font-weight: 300;
  color: var(--keys-text);
  background-color: var(--lap-results);
}

.panel--metric {
  margin: 10px;
  display: inline-block;
  font-size: x-large;
  font-weight: 600;
  color: var(--keys-text);
}

.stopwatch {
  position: absolute;
  top: 3%;
  right: 10%;
  font-size: xx-large;
  background-color: transparent;
  font-weight: 300;
  color: var(--keys-text);
}

.stopwatch--results {
  position: absolute;
  display: flex;
  flex-direction: column;
  left: 3%;
  top: 20%;
  font-size: x-large;
  padding: 10px;
  box-shadow: 0px 3px 7.5px var(--dark-shadow);
  border: none;
  background-color: var(--lap-results);
  color: var(--keys-text);
  border-radius: 8px;
}

.stopwatch--lap {
  padding: 5px;
  font-weight: 300;
  text-align: start;
  color: var(--keys-text);
}

.end {
  border: 1px solid black;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--primary-color);
  padding-top: 2%;
  color: var(--keys-text);
}

.end--results {
  display: flex;
  flex-direction: column;
  width: 40%;
  font-size: x-large;
  padding: 20px;
  box-shadow: 0px 2.98256px 7.4564px var(--dark-shadow);
  border: none;
  background-color: var(--primary-color);
  align-items: flex-start;
  color: var(--keys-text);
}

.congrats {
  font-size: 2rem;
  color: var(--keys-text);
}

.end--results--title {
  font-size: 1.4rem;
  color: var(--keys-text);
}

.end--laps {
  padding: 2%;
  margin: 0 auto;
  font-size: 1.7rem;
  color: var(--keys-text);
}

.end--reset {
  font-size: 1.5rem;
  padding: 0.6rem;
  border-radius: 8px;
  border: none;
  background-color: var(--secondary-color);
  color: var(--keys-text);
}

.end--reset:hover {
  box-shadow: 0px 2.98256px 7.4564px var(--dark-shadow);
  transition: 0.6s ease;
  cursor: pointer;
}

@media screen and (max-width: 1300px) {
  .stopwatch--lap {
    font-size: large;
  }
}
@media screen and (max-width: 1135px) {
  .remaining--letter,
  .previous--letter {
    font-size: 1.4rem;
  }
  .text {
    width: 44%;
  }
}

@media screen and (max-width: 1043px) {
  .remaining--letter,
  .previous--letter {
    font-size: 1.2rem;
  }
  .stopwatch {
    font-size: 1.4rem;
  }
  .panel--metric {
    font-size: 1.3rem;
  }

  .end--results {
    width: 55%;
  }
}

@media screen and (max-width: 925px) {
  .remaining--letter,
  .previous--letter {
    font-size: 1rem;
  }
  .stopwatch--lap {
    font-size: medium;
  }
  .stopwatch--results {
    padding: 10px;
  }

  .keyboard--key {
    height: 30px;
    width: 30px;
    font-size: 0.8rem;
  }

  .spacebar {
    height: 30px;
  }

  .keyboard--container {
    height: 160px;
  }
}

@media screen and (max-width: 831px) {
  .text > p {
    line-height: 1.1;
  }
  .tracker--panel--container {
    top: 28%;
  }
  .speed--panel,
  .accuracy--panel {
    width: 80%;
  }

  .panel--metric {
    font-size: 1rem;
  }
}
@media screen and (max-width: 745px) {
  .stopwatch--lap {
    font-size: small;
  }

  .end--results {
    width: 65%;
  }

  .panel--metric {
    font-size: 1rem;
  }

  .speed--panel,
  .accuracy--panel {
    font-size: 0.8rem;
  }

  .tracker--panel--container {
    min-width: 90px;
  }

  .text {
    width: 50%;
  }
}

@media screen and (max-width: 711px) {
  .stopwatch--results {
    padding: 0;
  }
}

@media screen and (max-width: 639px) {
  .keyboard--container {
    min-width: 80%;
  }
}

@media screen and (max-height: 810px) {
  .text > p {
    line-height: 1.1;
  }

  .tracker--panel--container {
    height: 50%;
  }

  .speed--panel,
  .accuracy--panel {
    height: 20%;
  }
}

@media screen and (max-height: 700px) {
  .text > p {
    line-height: 1;
  }
}

@media screen and (max-height: 630px) {
  .panel--metric {
    font-size: 1rem;
  }
  .remaining--letter,
  .previous--letter {
    font-size: 1rem;
  }
}

@media screen and (max-height: 590px) {
  .keyboard--key {
    height: 30px;
    width: 30px;
    font-size: 0.8rem;
  }

  .spacebar {
    height: 30px;
  }

  .keyboard--container {
    height: 160px;
  }
}

@media screen and (max-height: 550px) {
  .stopwatch--results {
    padding: 3px;
  }

  .speed--panel,
  .accuracy--panel {
    width: 80%;
    font-size: 0.8rem;
  }
}

